import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Company } from '../models/company';
import { Observable } from 'rxjs';
import { Customer } from '../models/customer';

@Injectable({
  providedIn: 'root'
})
export class AdminsService {

  private basePath = "http://localhost:8080/admin";

  constructor(private client: HttpClient) { }


  // -------------- Company --------------
  public addCompany(company: Company) {
    return this.client.post<Company>(this.basePath + "/addCompany/" + sessionStorage.token, company);
  }
  public updateCompany(company: Company) {
    return this.client.put<Company>(this.basePath + "/updateCompany/" + sessionStorage.token, company);
  }
  public deleteCompany(companyID: number) {
    return this.client.delete<number>(this.basePath + "/deleteCompany/" + sessionStorage.token + "/" + companyID);
  }
  public getAllCompanies(): Observable<Company[]> {
    return this.client.get<Company[]>(this.basePath + "/getAllCompany/" + sessionStorage.token);
  }
  public getOneCompany(companyID: number) {
    return this.client.get<Company>(this.basePath + "/getOneCompany/" + sessionStorage.token + "/" + companyID);
  }


  // -------------- Customer --------------
  public addCustomer(customer: Customer) {
    return this.client.post<Customer>(this.basePath + "/addCustomer/" + sessionStorage.token, customer);
  }
  public updateCustomer(customer: Customer) {
    return this.client.put<Customer>(this.basePath + "/updateCustomer/" + sessionStorage.token, customer);
  }
  public deleteCustomer(customerID: number) {
    return this.client.delete<number>(this.basePath + "/deleteCustomer/" + sessionStorage.token + "/" + customerID);
  }
  public getAllCustomer(): Observable<Customer[]> {
    return this.client.get<Customer[]>(this.basePath + "/getAllCustomers/" + sessionStorage.token);
  }
  public getOneCustomer(customerID: number) {
    return this.client.get<Customer>(this.basePath + "/getOneCustomer/" + sessionStorage.token + "/" + customerID);
  }

}
