import { Component, OnInit } from '@angular/core';
import { AdminsService } from 'src/app/services/admin.service';
import { Company } from 'src/app/models/company';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-admin-comp-getall',
  templateUrl: './admin-comp-getall.component.html',
  styleUrls: ['./admin-comp-getall.component.css']
})

export class AdminCompGetAllComponent implements OnInit {

  private companyList: Company[];

  constructor(
    private adminServ: AdminsService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    this.adminServ.getAllCompanies().subscribe((data) => {
      this.companyList = data;
    });
  }

  // get all companies
  public getAllCompanies(): void {
    this.adminServ.getAllCompanies().subscribe( (comp) => {
      // confirmation:
      console.log(comp); // console
      alert("Companies found successfully"); // alert
    }, error => {
      alert(error.error);
    });
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
