import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompanysService } from 'src/app/services/company.service';
import { Location } from '@angular/common';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-company-coup-all',
  templateUrl: './company-coup-all.component.html',
  styleUrls: ['./company-coup-all.component.css']
})
export class CompanyCoupAllComponent implements OnInit {

  private couponList: Coupon[];

  constructor(
    private compServ: CompanysService, 
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    this.compServ.getAllCoupons().subscribe((data) => {
      this.couponList = data;
    });
  }

  // get all companies - dont
  public getAllCoupons(): void {
    this.compServ.getAllCoupons().subscribe((coup) => {

      // confirmation:
      console.log(coup); // console
      alert("Companies found successfully!"); // alert
    }, error => {
      alert(error.error);
    });
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
