import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { CustomersService } from 'src/app/services/customer.service';
import { Coupon } from 'src/app/models/coupon';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-customer-cust-coupons-all',
  templateUrl: './customer-cust-coupons-all.component.html',
  styleUrls: ['./customer-cust-coupons-all.component.css']
})
export class CustomerCustCouponsAllComponent implements OnInit {

  private couponList: Coupon[];

  constructor(
     private custServ: CustomersService,
     private loginService: LoginService, 
     private router: Router, 
     private location: Location) { }

  ngOnInit() {
    // public getCustomerCoupons(): void {
      this.custServ.getCustomerCoupons().subscribe((coup) => {
        this.couponList = coup;
  
        // confirmation:
        console.log(coup); // console
        alert("Coupons loaded successfully"); // alert
      }, error => {
        alert(error.error);
      });
    // }
  }
  
  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
