import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompanysService } from 'src/app/services/company.service';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-company-coup-all-max',
  templateUrl: './company-coup-all-max.component.html',
  styleUrls: ['./company-coup-all-max.component.css']
})
export class CompanyCoupAllMaxComponent implements OnInit {

  private price: number;
  private couponList: Coupon[];

  constructor(
    private compServ: CompanysService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {

  }


  // get all companies
  public getCouponsBMaxprice(): void {
    if (this.price == undefined) {
      alert("Please input a price!");
    } else {
      this.compServ.getAllCouponsByMaxprice(this.price).subscribe((coup) => {
        this.couponList = coup;

        // confirmation:
        console.log(coup); // console
        alert("Coupons found successfully!"); // alert
      }, error => {
        alert(error.error);
      });
    }
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
