import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CustomersService } from 'src/app/services/customer.service';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-customer-coup-all',
  templateUrl: './customer-coup-all.component.html',
  styleUrls: ['./customer-coup-all.component.css']
})
export class CustomerCoupAllComponent implements OnInit {

  private couponList: Coupon[];

  constructor(
    private custServ: CustomersService, 
    private loginService: LoginService, 
    private router: Router, 
    private location: Location
  ) { }

  ngOnInit() {
    this.custServ.getAllCoupons().subscribe((coup) => {
      this.couponList = coup;

      // confirmation:
      console.log(coup); // console
      alert("Coupons loaded successfully"); // alert
    }, error => {
      alert(error.error);
    });
  }
      
  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
