import { Component, OnInit } from '@angular/core';
import { Customer } from 'src/app/models/customer';
import { AdminsService } from 'src/app/services/admin.service';
import { Location } from '@angular/common';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-admin-cust-add',
  templateUrl: './admin-cust-add.component.html',
  styleUrls: ['./admin-cust-add.component.css']
})

export class AdminCustAddComponent implements OnInit {

  private customer: Customer;

  constructor(
    private adminServ: AdminsService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    this.customer = new Customer();
  }

   // add customer
   public addCustomer(): void{
    this.adminServ.addCustomer(this.customer).subscribe( (cust)=>{
      
      // confirmation:
      console.log(cust); // console
      alert("customer added successfully"); // alert
    }, error => {
      alert(error.error);
    });
  }
  
  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
