import { Component, OnInit } from '@angular/core';
import { Category } from 'src/app/models/category';
import { Coupon } from 'src/app/models/coupon';
import { CustomersService } from 'src/app/services/customer.service';
import { Location } from '@angular/common';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-customer-coup-all-cat',
  templateUrl: './customer-coup-all-cat.component.html',
  styleUrls: ['./customer-coup-all-cat.component.css']
})

export class CustomerCoupAllCatComponent implements OnInit {

  private category: Category;
  private couponList: Coupon[];

  constructor(
    private custServ: CustomersService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {

  }

  // get coupons by category
  public getCouponsByCat(): void {
    if (this.category == undefined) {
      alert("Please choose a category!");
    } else {
      this.custServ.getCustomerCouponsByCategory(this.category).subscribe((coup) => {
        this.couponList = coup;

        // confirmation:
        console.log(coup); // console
        alert("Coupons found successfully!"); // alert
      }, error => {
        alert(error.error);
      });
    }
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
