import { Component, OnInit } from '@angular/core';
import { CompanysService } from 'src/app/services/company.service';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-company-coup-delete',
  templateUrl: './company-coup-delete.component.html',
  styleUrls: ['./company-coup-delete.component.css']
})
export class CompanyCoupDeleteComponent implements OnInit {

  private couponID: number;

  constructor(
    private compServ: CompanysService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() { }

  // delete coupon
  public deleteCoupon(): void {
    if (this.couponID == undefined) {
      alert("The input Coupon ID is empty!");
    } else {
      this.compServ.deleteCoupon(this.couponID).subscribe((coup) => {

        // confirmation:
        console.log(coup); // console
        alert("Coupon deleted successfully!"); // alert
      }, error => {
        alert(error.error);
      });
    }
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
