import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';


@Injectable({
  providedIn: 'root'
})
export class LoginService {

  private basePath = "http://localhost:8080";

  constructor(private client: HttpClient) { }

  public login(email: string, pass: string, type: string) {
    return this.client.post(this.basePath + "/login/" + email + "/" + pass + "/" + type,
      null, { responseType: 'text' });
  }

  public logout(login: any) {
    return this.client.post(this.basePath + "/logout/" + sessionStorage.token, login);
  }



}
