import { Component, OnInit } from '@angular/core';
import { Company } from 'src/app/models/company';
import { AdminsService } from 'src/app/services/admin.service';
import { Location } from '@angular/common';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-admin-comp-delete',
  templateUrl: './admin-comp-delete.component.html',
  styleUrls: ['./admin-comp-delete.component.css']
})

export class AdminCompDeleteComponent implements OnInit {

  private companyID: number;

  constructor(
    private adminServ: AdminsService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() { }

  // delete company:
  public deleteCompany(): void {
    if(this.companyID==undefined){
      alert("Your ID number is empty");
    }else{
      this.adminServ.deleteCompany(this.companyID).subscribe((id) => {
  
        // confirmation:
        console.log(id); // console
        alert("Company " + id + " deleted successfully"); // alert
      }, error => {
        alert(error.error);
      });
    }
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}