import { Component, OnInit } from '@angular/core';
import { AdminsService } from 'src/app/services/admin.service';
import { Company } from 'src/app/models/company';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-admin-comp-getone',
  templateUrl: './admin-comp-getone.component.html',
  styleUrls: ['./admin-comp-getone.component.css']
})

export class AdminCompGetOneComponent implements OnInit {

  private company: Company;
  private companyID: number;

  constructor(
    private adminServ: AdminsService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    this.company = new Company();
  }

  // get one company
  public getOneCompany(): void {
    if (this.companyID == undefined) {
      alert("Your ID number is empty!");
    } else {
      this.adminServ.getOneCompany(this.companyID).subscribe((comp) => {
        this.company = comp;

        // confirmation:
        console.log(comp); // console
        alert("Companies found successfully!"); // alert
      }, error => {
        alert(error.error);
      });
    }
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}