import { Component, OnInit } from '@angular/core';
import { Customer } from 'src/app/models/customer';
import { AdminsService } from 'src/app/services/admin.service';
import { Location } from '@angular/common';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-admin-cust-update',
  templateUrl: './admin-cust-update.component.html',
  styleUrls: ['./admin-cust-update.component.css']
})

export class AdminCustUpdateComponent implements OnInit {

  private customer: Customer;

  constructor(
    private adminServ: AdminsService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    this.customer = new Customer();
  }

  // update customer - works
  public updateCustomer(): void {
    this.adminServ.updateCustomer(this.customer).subscribe((cust) => {
      alert("Customer " + cust + " updated successfully.");

      // confirmation:
      console.log(cust); // console
      alert("Customer updated successfully!"); // alert
    }, error => {
      alert(error.error);
    });
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
