import { Component, OnInit } from '@angular/core';
import { Customer } from 'src/app/models/customer';
import { AdminsService } from 'src/app/services/admin.service';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-admin-cust-getall',
  templateUrl: './admin-cust-getall.component.html',
  styleUrls: ['./admin-cust-getall.component.css']
})

export class AdminCustGetallComponent implements OnInit {

  private customerList: Customer[];

  constructor(
    private adminServ: AdminsService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    this.adminServ.getAllCustomer().subscribe((cust) => {
      this.customerList = cust;
    });
  }

  // get all customers
  public getAllCustomer(): void {
    this.adminServ.getAllCustomer().subscribe((data) => {

      // confirmation:
      console.log(data); // console
      alert("Coupon updated successfully"); // alert
    }, error => {
      alert(error.error);
    });
  }
  
  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
