import { Component, OnInit } from '@angular/core';
import { Customer } from 'src/app/models/customer';
import { AdminsService } from 'src/app/services/admin.service';
import { Location } from '@angular/common';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-admin-cust-delete',
  templateUrl: './admin-cust-delete.component.html',
  styleUrls: ['./admin-cust-delete.component.css']
})

export class AdminCustDeleteComponent implements OnInit {

  private customerID: number;

  constructor(
    private adminServ: AdminsService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() { }

  // delete customer
  public deleteCustomer(): void {
    if (this.customerID == undefined) {
      alert("Your ID number is empty!")
    } else {
      this.adminServ.deleteCustomer(this.customerID).subscribe((cust) => {

        // confirmation:
        console.log(cust); // console
        alert("Customer deleted successfully!"); // alert
      }, error => {
        alert(error.error);
      });
    }
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
