import { Component, OnInit } from '@angular/core';
import { AdminsService } from 'src/app/services/admin.service';
import { Customer } from 'src/app/models/customer';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-admin-cust-getone',
  templateUrl: './admin-cust-getone.component.html',
  styleUrls: ['./admin-cust-getone.component.css']
})

export class AdminCustGetoneComponent implements OnInit {

  private customer: Customer;
  private customerID: number;

  constructor(
    private adminServ: AdminsService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    this.customer = new Customer();
  }

  // get one customer
  public getOneCustomer(): void {
    if (this.customerID == undefined) {
      alert("You ID number is empty!")
    } else {
      this.adminServ.getOneCustomer(this.customerID).subscribe((cust) => {
        this.customer = cust;

        // confirmation:
        console.log(cust); // console
        alert("Customer was found successfully!"); // alert
      }, error => {
        alert(error.error);
      });
    }
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}


