import { Component, OnInit } from '@angular/core';
import { CustomerComponent } from '../customer/customer.component';
import { CustomersService } from 'src/app/services/customer.service';
import { Customer } from 'src/app/models/customer';
import { Location } from '@angular/common';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-customer-details',
  templateUrl: './customer-details.component.html',
  styleUrls: ['./customer-details.component.css']
})
export class CustomerDetailsComponent implements OnInit {

  private customer: Customer;

  constructor(
    private custServ: CustomersService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    this.customer = new Customer();

    this.custServ.getCustomerDetails().subscribe((cust) => {
      this.customer = cust;

      // confirmation:
      console.log(cust); // console
      alert("Your information loaded successfully!"); // alert
    }, error => {
      alert(error.error);
    });
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}




