import { Component, OnInit } from '@angular/core';
import { CustomersService } from 'src/app/services/customer.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-customer-coup-purchase',
  templateUrl: './customer-coup-purchase.component.html',
  styleUrls: ['./customer-coup-purchase.component.css']
})
export class CustomerCoupPurchaseComponent implements OnInit {

  private couponID: number;

  constructor(
    private custServ: CustomersService, 
    private loginService: LoginService, 
    private router: Router, 
    private location: Location
  ) { }

  ngOnInit() {
  }

  // add coupon
  public purchaseCoupon() {
    if (this.couponID == undefined) {
      alert("Input the coupon id!");
    } else {
      this.custServ.purchaseCoupon(this.couponID).subscribe((coupon) => {

        // confirmation:
        console.log(coupon); // console
        alert("Coupon purchased successfully"); // alert

      }, error => {
        alert(error.error);
      });
    }
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
