import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders, HttpRequest, HttpHandler, HttpEvent } from '@angular/common/http';
import { Observable, throwError, observable } from 'rxjs';
import { Customer } from '../models/customer';
import { Coupon } from '../models/coupon';
import { Category } from '../models/category';
import { retry, catchError } from 'rxjs/operators';



@Injectable({
  providedIn: 'root'
})
export class CustomersService {

  private basePath = "http://localhost:8080/customer";

  constructor(private client: HttpClient) { }

  public fetch() {
    return this.client
  }

  public purchaseCoupon(couponID: number) {
    return this.client.post<number>(this.basePath + "/purchaseCoupon/" + sessionStorage.token + "/" + couponID, Coupon);
  }
  public getCustomerCoupons(): Observable<Coupon[]> {
    return this.client.get<Coupon[]>(this.basePath + "/getCustomerCoupons/" + sessionStorage.token);
  }
  public getCustomerCouponsByCategory(category: Category): Observable<Coupon[]> {
    return this.client.get<Coupon[]>(this.basePath + "/getCustomerCouponsByCategory/" + sessionStorage.token + "/" + category);
  }
  public getCustomerCouponsByMaxprice(price: number): Observable<Coupon[]> {
    return this.client.get<Coupon[]>(this.basePath + "/getCustomerCouponsByMaxprice/" + sessionStorage.token + "/" + price);
  }
  public getCustomerDetails(): Observable<Customer> {
    return this.client.get<Customer>(this.basePath + "/getCustomerDetails/" + sessionStorage.token);
  }
  public getAllCoupons(): Observable<Coupon[]> {
    return this.client.get<Coupon[]>(this.basePath + "/getAllCoupons/" + sessionStorage.token);
  }

}
