import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Company } from '../models/company';
import { Observable } from 'rxjs';
import { Coupon } from '../models/coupon';
import { Category } from '../models/category';

@Injectable({
  providedIn: 'root'
})
export class CompanysService {

  private basePath = "http://localhost:8080/company";

  constructor(private client: HttpClient) { }

  public addCoupon(coupon: Coupon) {
    return this.client.post<Company>(this.basePath + "/addCoupon/" + sessionStorage.token, coupon);
  }

  public updateCoupon(coupon: Coupon) {
    return this.client.put<Coupon>(this.basePath + "/updateCoupon/" + sessionStorage.token, coupon);
  }

  public deleteCoupon(couponID: number) {
    return this.client.delete<number>(this.basePath + "/deleteCoupon/" + sessionStorage.token + "/" + couponID);
  }

  public getOneCoupon(couponID: number): Observable<Coupon> {
    return this.client.get<Coupon>(this.basePath + "/getOneCoupon/" + sessionStorage.token + "/" + couponID);
  }

  public getAllCoupons(): Observable<Coupon[]> {
    return this.client.get<Coupon[]>(this.basePath + "/getCompanyCoupons/" + sessionStorage.token);
  }

  public getCompanyCouponsCategory(category: Category): Observable<Coupon[]> {
    return this.client.get<Coupon[]>(this.basePath + "/getCompanyCouponsCategory/" + sessionStorage.token + "/" + category);
  }

  public getAllCouponsByMaxprice(price: number): Observable<Coupon[]> {
    return this.client.get<Coupon[]>(this.basePath + "/getCompanyCouponsMaxprice/" + sessionStorage.token + "/" + price);
  }

  public getCompanyDetails(): Observable<Company> {
    return this.client.get<Company>(this.basePath + "/getCompanyDetails/" + sessionStorage.token);
  }

}
