import { Component, OnInit } from '@angular/core';
import { Company } from 'src/app/models/company';
import { AdminsService } from 'src/app/services/admin.service';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-admin-comp-add',
  templateUrl: './admin-comp-add.component.html',
  styleUrls: ['./admin-comp-add.component.css']
})

export class AdminCompAddComponent implements OnInit {

  private company: Company;

  constructor(
    private adminServ: AdminsService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    this.company = new Company();
  }

  // add company - works
  public addCompany(): void {
    this.adminServ.addCompany(this.company).subscribe((comp) => {

      // confirmation:
      console.log(comp); // console
      alert("Company added successfully"); // alert
    }, error => {
      alert(error.error);
    });

  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
