import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompanysService } from 'src/app/services/company.service';
import { Location } from '@angular/common';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-company-coup-update',
  templateUrl: './company-coup-update.component.html',
  styleUrls: ['./company-coup-update.component.css']
})
export class CompanyCoupUpdateComponent implements OnInit {

  private coupon: Coupon;

  constructor(
    private compServ: CompanysService, 
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    this.coupon = new Coupon();
  }

  // update coupon
  public updateCoupon(): void {
    console.log(this.coupon);
    // checks
    if (this.coupon.amount == undefined || this.coupon.couponID== undefined || this.coupon.price == undefined ) {
      alert("VC:One of your fields is empty!");
    } else {
      // the update
      this.compServ.updateCoupon(this.coupon).subscribe((coup) => {

        // confirmation:
        console.log(coup); // console
        alert("Coupon updated successfully"); // alert
      }, error => {
        alert(error.error);
      });
    }
  }
    
  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

  public getCoupon(id: number): void {
    this.compServ.getOneCoupon(id).subscribe((data) => {
      this.coupon = data;

      // confirmation:
      console.log(data); // console
      alert("Coupon updated successfully"); // alert
    }, error => {
      alert(error.error);
    });
  }

}
