import { Component, OnInit } from '@angular/core';
import { CompanysService } from 'src/app/services/company.service';
import { Company } from 'src/app/models/company';
import { Location } from '@angular/common';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-company-details',
  templateUrl: './company-details.component.html',
  styleUrls: ['./company-details.component.css']
})

export class CompanyDetailsComponent implements OnInit {

  private company: Company;

  constructor(
    private compServ: CompanysService, 
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    this.company = new Company();
    
    this.compServ.getCompanyDetails().subscribe((comp) => {
      this.company = comp;

      // confirmation:
      console.log(comp); // console
      alert("Company loaded successfully!"); // alert
    }, error => {
      alert(error.error);
    });
  }
    
  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
