import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompanysService } from 'src/app/services/company.service';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-company-coup-add',
  templateUrl: './company-coup-add.component.html',
  styleUrls: ['./company-coup-add.component.css']
})

export class CompanyCoupAddComponent implements OnInit {

  private coupon: Coupon;

  constructor(
    private compServ: CompanysService,
    private loginService: LoginService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    this.coupon = new Coupon();
  }

  // add company - works
  public addCoupon(): void {
    console.log(this.coupon); // console

    if (this.coupon.amount == undefined || this.coupon.price == undefined) {
      alert("Incorrect input!")
    } else {
      this.compServ.addCoupon(this.coupon).subscribe((coup) => {

        // confirmation:
        console.log(coup); // console
        alert("Coupon added successfully!"); // alert
      }, error => {
        alert(error.error);
      });
    }
  }

  public backClicked(): void {
    this.location.back();
  }

  public logout(): void {
    this.loginService.logout(sessionStorage.token).subscribe((response) => {
      this.router.navigate(["/home"]);
    });
  }

}
